.cart-list {
  height: 85%;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-left: 30px;
  font-family: "Barlow Condensed", sans-serif;
}

.cart-item {
  margin-top: 5px;
  margin-bottom: 5px;
  width: 100%;
  height: 30%;
  background-color: white;
  padding: 10px;
  border: 0.5px solid gray;
  border-radius: 5px;
  position: relative;
}

.remove-item-icon {
  cursor: pointer;
  position: absolute;
  top: 5%;
  right: 5%;
  font-size: 22px;
}

.cart-item-img {
  height: 100%;
  object-fit: contain;
}

/* .cart-item-img-container {
  min-width: 100px;
} */

.cart-item-info {
  margin-left: 10px;
}

.cart-item-info p {
  margin: 0;
}

.quantity-icons {
  margin-left: 15px;
}

.change-quantity-icon {
  cursor: pointer;
}

.quantity {
  -webkit-appearance: none; 
  margin: 0;
}

.no-data {
  padding-top: 70%;
  margin-left: 30px;
}

.button-container {
  position: absolute;
  bottom: 1%;
  width: 100%;
  padding-top: 3%;
  left: 0;
  background-color: #f7f7f8;
}

.cart-list::-webkit-scrollbar {
  width: 0px;  /* Remove scrollbar space */
  background: transparent;  /* Optional: just make scrollbar invisible */
}
