#tracking-form, #order-status-form {
  height: 100%;
  margin-top: 1.5vw;
  padding: 1.5vw;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.tracking-page-container {
  padding-top: 2vw;
  padding-bottom: 2vw;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  min-height: 65vh;
  background-image: linear-gradient(
    rgba(4, 101, 85, 1),
    rgba(4, 101, 85, 0.7),
    rgba(4, 101, 85, 0.5)
  );
  font-family: "Barlow Condensed", sans-serif;
}
