.best-seller {
  /* background-color: #046555; */
  background-image: linear-gradient(white, #046555);
  padding-left: 5%;
  padding-right: 5%;
}

.best-seller hr {
  margin-top: 3.5vw;
  width: 20%;
  border-width: 10px;
  background-color: white;
}

.best-seller-title {
  margin: 0;
  padding: 0;
  /* font-size: 70px;
  font-family: 'Courgette', cursive; */

  font-size: 6vw;
  font-weight: bold;
  font-family: "Barlow Condensed", sans-serif;
}

.of-all-time-title {
  /* margin-top: -35px; */
  padding: 0;
  /* font-size: 50px;
  font-family: 'Roboto', sans-serif; */
  font-size: 3vw;
  font-family: "Courgette", cursive;
}

@media screen and (max-width: 650px) {
  .of-all-time-title {
    font-size: 5vw;
    font-weight: bolder;
  }
  .best-seller-title {
    font-size: 10vw;
  }
  .best-seller hr {
    border-width: 5px;
  }
}
