.hit-cards {
  background-color: #046555;
  padding-left: 5%;
  padding-right: 5%;
}

.hit-cards hr {
  margin-top: 3.5vw;
  width: 20%;
  border-width: 10px;
  background-color: white;
}

.hit-cards-title {
  margin: 0;
  padding: 0;
  /* font-size: 70px;
  font-family: 'Courgette', cursive; */
  color: #ffffff;

  font-size: 6vw;
  font-weight: bold;
  font-family: "Barlow Condensed", sans-serif;
}

.in-this-weak-title {
  padding: 0;
  /* font-size: 50px;
  font-family: 'Roboto', sans-serif; */
  color: #ffffff;
  font-size: 3vw;
  font-family: "Courgette", cursive;
}

@media screen and (max-width: 650px) {
  .hit-cards-title {
    font-size: 10vw;
    font-weight: bolder;
  }
  .in-this-weak-title {
    font-size: 5vw;
  }
  .hit-cards hr {
    border-width: 5px
  }
}


