}

/* Display the columns below each other instead of side by side on small screens */
@media screen and (max-width: 650px) {
  .header-container {
    background-attachment: scroll;
  }
  .header-title-text {
    margin-top: 3vw;
    font-size: 15vw;
  }
  .header-description-text {
    font-size: 5vw;
  }

  .content {
    /* padding: 2vw; */
    height: auto;
  }

  .header-img-container {
    /* width: 0vw;
    height: 0; */
    display: none;
    /* flex-wrap: nowrap; */
    /* display: flex;
    flex-direction: row;
    justify-content: space-between; */
  }
  .nav-link {
    font-size: 4.5vw;
  }
}

@media (max-width: 768px) {
  .header-img-container {
    /* width: 0vw;
    height: 0; */
    display: none;
  }
  .header-container {
  }
}

.modal-dialog {
  position: relative;
  display: table; /* This is important */
  overflow-y: auto;
  overflow-x: auto;
  width: auto;
  min-width: 300px;
}
