.close-cart-icon {
  cursor: pointer;
  font-size: 30px;
  margin-left: 20px;
  margin-top: 20px;
}

.cart-title-container {
  z-index: -10;
  position: absolute;
  top: 0;
  width: 100%;
}

.cart-title {
  font-size: 40px;
  /* font-family: "Dancing Script", cursive; */

  font-family: "Barlow Condensed", cursive;
}
.search-bar :focus {
  outline: none;
}

.cart-quantity {
  position: absolute;
  color: #000000;
  background-color: #FFC107;
  text-align: center;
  border-radius: 100px;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .cart-quantity {
    height: 15px;
    width: 15px;
    font-size: 15px;
    top: 12px;
    right: 10px;
    line-height: 15px;
  }
} 

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .cart-quantity {
    height: 20px;
    width: 20px;
    font-size: 18px;
    top: 18px;
    right: 10px;
    line-height: 18px;
  }
} 

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .cart-quantity {
    height: 15px;
    width: 15px;
    font-size: 15px;
    top: 12px;
    right: 10px;
    line-height: 15px;
  }
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .cart-quantity {
    height: 18px;
    width: 18px;
    font-size: 16px;
    top: 12px;
    right: 12px;
    line-height: 16px;
  }
} 

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .cart-quantity {
    height: 25px;
    width: 25px;
    font-size: 22px;
    top: 12px;
    right: 10px;
    line-height: 22px;
  }
}