.filter {
  padding-left: 20px;
  color: whitesmoke;
}

.filter hr {
  border: 2px solid whitesmoke;
}

.filter-item {
  cursor: pointer;
  margin-bottom: 10px;
  padding-left: 5px;
}

.filter-item:hover {
  color: #000000;
  background-color: #ffffff;
}

.sub-filter {
  margin-left: 30px;
  margin-bottom: 10px;
}

.sub-filter-item {
  cursor: pointer;
  padding-left: 5px;
}

.sub-filter-item:hover {
  color: #000000;
  background-color: #ffffff;
}

h5 {
  margin: 0;
  margin-left: 5px; 
}