/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .cart-alert {
    position: fixed;
    top: 20px;
    right: 20px;
    left: 20px;
    z-index: 90;
  }
  
  .item-alert {
    width: 100%;
    overflow: hidden;
  }
} 

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .cart-alert {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 90;
  }
  
  .item-alert {
    width: 400px;
    overflow: hidden;
  }
} 

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .cart-alert {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 90;
  }
  
  .item-alert {
    width: 350px;
    overflow: hidden;
  }
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .cart-alert {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 90;
  }
  
  .item-alert {
    width: 450px;
    overflow: hidden;
  }
} 

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .cart-alert {
    position: fixed;
    top: 20px;
    right: 20px;
    z-index: 90;
  }
  
  .item-alert {
    width: 500px;
    overflow: hidden;
  }
}