@font-face {
  font-family: "stars__stripesregular";
  src: url("../../../../StarsStripes.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.go-to-store {
  background-image: linear-gradient(
    rgba(4, 101, 85, 1),
    rgba(4, 101, 85, 0.7),
    rgba(4, 101, 85, 0.5)
  );
  padding-top: 3vw;
  padding-bottom: 4vw;
  font-family: "Barlow Condensed", sans-serif;
}

.go-to-store p {
  margin: 0%;
  color: #ffffff;
}

.go-to-store-title {
  font-size: 7.75vw;
  font-family: "stars__stripesregular", cursive;
}

.go-to-store-content {
  font-size: 3.25vw;
}

.go-to-store-slogan {
  font-size: 4.25vw;
  font-weight: bolder;
}

.go-to-store hr {
  margin-top: 3.5vw;
  width: 20%;
  border-width: 10px;
  background-color: white;
}

.btn-txt {
  font-size: 2.75vw;
}

@media screen and (max-width: 650px) {
  .go-to-store p {
    margin: 0.75%;
  }
  .go-to-store-title {
    font-size: 10vw;
  }

  .go-to-store-content {
    font-size: 3.95vw;
  }

  .go-to-store-slogan {
    font-size: 4.5vw;
  }

  .go-to-store hr {
    border-width: 5px;
  }
  .btn-txt {
    font-size: 4.75vw;
  }
}
