.order-page-container {
  /* margin-top: 2vw;
  margin-bottom: 2vw; */
  height: 100%;
  padding-top: 2vw;
  padding-bottom: 2vw;
  display: flex;
  flex-direction: row;
  width: 100%;
  min-height:65vh;
  font-family: "Barlow Condensed", sans-serif;

  background-image: linear-gradient(
    rgba(4, 101, 85, 1),
    rgba(4, 101, 85, 0.7),
    rgba(4, 101, 85, 0.5)
  );

  /* padding: 5vw;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19); */
}

.order-info-container {
  min-height:100%;
}
.order-item-container {
  margin-top: 1.5vw;
  padding: 1vw;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.price-small-txt {
  /* text-align: right; */
  font-size: 1.5vw;
}
.price-txt {
  text-align: right;
  font-size: 2vw;
}
.cash-info {
  margin-top: 3vw;
  background-color: whitesmoke;
  font-size: 1.3vw;
  border-radius: 5px;
  padding: 2vw;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.ship-info {
  /* background-color: #469cc7; */
  margin-top: 2vw;
  border-radius: 5px;
  padding: 2vw;
  font-size: 1.3vw;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.order-btn {
  /* background-color: #c77146; */
  background-color: #1f78d1;
  color: white;
  margin-top: 20px;
  border-radius: 5px;
  opacity: 0.9;
  transition: 0.3s;
  font-weight: bold;
  /* font-size: 2vw; */
}
.order-btn:hover {
  opacity: 1;
}
.link:hover {
  text-decoration: underline;
  color: #469cc7;
  /* color: white; */
  cursor: pointer;
}

.empty-cart {
  width: 100vw;
  margin-top: 4vw;
  padding: 4vw
}

@media screen and (max-width: 650px) {
  .price-small-txt {
    /* font-size: 1.5vw; */
    font-size: 3.5vw;
  }
  .price-txt {
    /* font-size: 2vw; */
    font-size: 4.5vw;
  }
  .cash-info {
    /* font-size: 1.3vw; */
    font-size: 3.25vw;
  }

  .ship-info {
    /* font-size: 1.3vw; */
    font-size: 3vw;
  }
}
