.product-list-page {
  padding-top: 5vh;
  margin: 0;
  font-family: "Barlow Condensed", sans-serif;
  background-image: linear-gradient(
    rgba(4, 101, 85, 1),
    rgba(4, 101, 85, 0.7),
    rgba(4, 101, 85, 0.5)
  );
  position: relative;
}
.product-list {
  width: 100%;
  min-height: 65vh;
}

.product-item {
  width: 20%;
  margin: 20px;
  border: 1px solid;
  border-radius: 5px;
  background-color: whitesmoke;
}

.product-item-info {
  padding-top: 6px;
  padding-bottom: 10px;
}

.add-to-cart {
  width: 70%;
}

.product-item-info #name {
  cursor: pointer;
  margin: 0;
  margin-left: 16px;
}

.product-item-info #price {
  cursor: pointer;
  color: #FF4500;
  margin-left: 16px;
}

.pagination {
  width: 95%;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .product-list{
    justify-content: center;
    min-height: 35vh;
  }
  .product-item{
    width: 45%;
    margin: 5px;
  }
  .product-item-info #name {
    font-size: 25px;
  }
  .product-item-info #price {
    font-size: 20px;
  }
  .add-to-cart {
    font-size: 13px;
  }
} 

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  .product-list{
    /* justify-content: center; */
    min-height: 35vh;
  }
  .product-item{
    width: 40%;
    margin: 5px;
  }
  .product-item-info #name {
    font-size: 30px;
  }
  .product-item-info #price {
    font-size: 25px;
  }
  .add-to-cart {
    font-size: 22px;
  }
} 

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .product-list{
    /* justify-content: center; */
    min-height: 35vh;
  }
  .product-item{
    width: 30%;
    margin: 5px;
  }
  .product-item-info #name {
    font-size: 20px;
  }
  .product-item-info #price {
    font-size: 16px;
  }
  .add-to-cart {
    font-size: 15px;
  }
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .product-list{
    /* justify-content: center; */
    min-height: 65vh;
    padding-left: 5vw;
  }
  .product-item{
    width: 25%;
    margin: 10px;
  }
  .product-item-info #name {
    font-size: 22px;
  }
  .product-item-info #price {
    font-size: 18px;
  }
  .add-to-cart {
    font-size: 15px;
  }
} 

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .product-list{
    /* justify-content: center; */
    min-height: 65vh;
    padding-left: 5vw;
  }
  .product-item{
    width: 20%;
    margin: 10px;
  }
  .product-item-info #name {
    font-size: 24px;
  }
  .product-item-info #price {
    font-size: 20px;
  }
  .add-to-cart {
    font-size: 18px;
  }
}

.pagination-item {
  background-color: #FFFFFF;
  width: 40px;
  margin-left: 3px;
  margin-right: 3px;
  border: 1px solid #17a2b8;
  border-radius: 5px;
}

.pagination-link {
  color: #17a2b8;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
  padding-top: 10px;
  padding-bottom: 10px;
}

.pagination-item.selected>.pagination-link:hover {
  text-decoration: none;
  color: white;
}

.active-page-item {
  background-color: #17a2b8;
  color: white;
}

.next {
  background-color: #FFFFFF;
  border: 1px solid #17a2b8;
  border-radius: 5px;
  width: 40px;
}

.next-link {
  color: #17a2b8;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.next-link:hover{
  text-decoration: none;
  color: #17a2b8;
}

.prev {
  background-color: #FFFFFF;
  border: 1px solid #17a2b8;
  border-radius: 5px;
  width: 40px;
}

.prev-link {
  color: #17a2b8;
  display: block;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.prev-link:hover {
  text-decoration: none;
  color: #17a2b8;
}