.card-list {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 40px;
  padding-right: 40px;
  background-color: rgba(255, 255, 255, 0);
  font-family: "Barlow Condensed", sans-serif;
}

.card-item-container {
  background-color: #ffffff;
  /* width: 16%; */
  width: unset !important;
  padding: 5px;
  margin-left: 5px;
  margin-right: 5px;
  position: relative;
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  width: 100%;
  height: 0;
  transition: .5s ease;
  background-color: rgba(255, 255, 255, 0.7)
}

.card-item-container:hover .overlay {
  height: 100%;
}

.detail-btn {
}

#item-name {
  font-size: 25px;
  color: #046555;
}

#item-price {
  font-size: 20px;
  color: #078470;
}

.slide-show {
  position: relative;
  align-items: 'center';
  width: 100%;
}

