.product-info-container {
  width: 100%;
  font-family: "Barlow Condensed", sans-serif;
  background-image: linear-gradient(
    rgba(4, 101, 85, 1),
    rgba(4, 101, 85, 0.7),
    rgba(4, 101, 85, 0.5)
  );
  /* box-sizing: content-box; */
  /* height: auto; */
  /* padding: 2.5vw; */
  /* padding: 150px;
  padding-top: 50px; */
}
.row {
  box-sizing: content-box;
}
.product-title-txt,
.related-products-title-txt {
  font-style: normal;
  font-weight: bold;
  font-size: 3vw;
}
.product-title-secondary-txt {
  font-style: normal;
  font-weight: bold;
  font-size: 2.5vw;
}

.product-category-txt {
  font-style: normal;
  font-weight: bold;
  font-size: 2vw;
}

.product-info-txt {
  font-style: normal;
  font-weight: 400;
  font-size: 1.7vw;
}

.top-container {
  margin-top: 4vw;
}

.bottom-container {
  margin-top: 2vw;
  background-color: whitesmoke;
  border-radius: 5px;
  padding: 2vw;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.card-info-img {
  margin-top: 2vw;
  background-color: whitesmoke;
  padding: 5px;
  border-radius: 5px;
  /* width: 100%;
  height: 50%; */
  object-fit: contain;
  /* height: auto; */
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.related-products-container {
  width: 100%;
  margin-top: 2vw;
  padding: 2vw;
  background-color: #438d80;
  border-radius: 5px;
  margin-bottom: 2vw;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.related-products-title-txt {
  color: white;
}

.card-details-info {
  display: flex;
  flex-direction: column;
  
  justify-content: space-between;
  margin-top: 2vw;
  background-color: whitesmoke;
  border-radius: 5px;
  padding: 2vw;
  box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.add-to-cart-btn {
  width: 50%;
}

@media screen and (max-width: 650px) {
  .product-title-txt,
  .related-products-title-txt {
    font-size: 7.5vw;
  }
  .product-title-secondary-txt {
    /* font-size: 2.5vw; */
    font-size: 5vw;
  }

  .product-category-txt {
    /* font-size: 2vw; */
    font-size: 4.5vw;
  }

  .product-info-txt {
    /* font-size: 1.7vw; */

    font-size: 3.75vw;
  }

  .add-to-cart-btn {
    width: 100%;
  }
}
